.projects {
	height: 100%;
}

.card {
	background-color: var(--background-color);
	border-radius: 25px;
	font-size: 20px;
	color: var(--text-color);
	transition: background-color 250ms linear, color 250ms linear;
}
.tab {
	font-size: 20px;
}
.card:global(.ant-card .ant-card-body) {
	padding: 20px 15px;
}
.card .card__image {
	width: 100%;
	border-radius: 20px;
}
.card__title {
	padding-top: 20px;
}
.tag {
	padding-bottom: 10px;
}

@media (max-width: 768px) {
	.ant-card .ant-card-body {
		padding: 15px;
	}
}
